<template>
  <div>
    <!-- 리터럴 배열 요소 값으로 삼항식 조건 사용하기 -->
    <span v-bind:class="[isActive ? activeClass : '', errorClass]"
      >active,text-danger css 클래스 적용하기</span
    >
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeClass: "active",
      errorClass: "text-danger",
      isActive: false, // 기본적으로 클래스가 적용되지않음
    };
  },
};
</script>
<style>
span.active {
  border: solid 1px blue;
}
span.text-danger {
  color: red;
}
</style>
